.monthlyCalendar__monthBlock{
  min-height: 190px;
  
}

.monthlyCalendar__weekBox {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: auto;
  /* box-sizing: border-box; */
}

.monthlyCalendar__weekNamesBox {
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

.monthlyCalendar__weekDays {
  text-align: center;
  vertical-align: center;
 -ms-flex-preferred-size: 13%;
     flex-basis: 13%;
 min-height: 1rem;
 font-size: 14px;
 margin: 5px 0;
}

.monthlyCalendar__weekDays_withDate {
  /* border: 0.1px solid black; */
background-color: antiquewhite;
border-radius: 5px;
cursor: pointer;
}

.monthlyCalendar__selectedDate {
  background-color: lightcoral;
  color:white;
}




