
.appStageDate__box {
  background-color: floralwhite;
  border-radius: 5px;
  padding: 20px;
  border: 1px solid lightcoral;
}

.appStageDate__selectPeriod {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 10px 0 35px;
}

.appStageDate__periodInfo {
  display: inline-block;
  color: lightcoral;
}

.appStageDate__changePeriod {
  background-color: gainsboro;
  color: lightcoral;
  border-radius: 5px;
  cursor: pointer;
}
.appStageDate__changePeriod:disabled{
  color: inherit;
}


